/* ===== UA / (,  ) ===== */
button, [role="button"] {
  appearance: none !important;
  -webkit-appearance: none !important;
  -moz-appearance: none !important;
}
button::-webkit-outer-spin-button,
button::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin_button,
input[type="number"]::-webkit-inner-spin_button {
  -webkit-appearance: none !important;
  margin: 0 !important;
}

/* ===== () Minecraftia        ===== */
/*    or     */
@font-face {
  font-family: 'Minecraftia';
  src: url('https://fonts.cdnfonts.com/s/17245/Minecraftia-Regular.woff') format('woff');
  font-display: swap;
}

/* =====================================================================
   Minecraft Style Skin (      )
   ===================================================================== */

/*  ( ) */
.mc-card {
  --h: 56px;         /* /   */
  --pad: 12px;
  --w: 720px;
  --mt: 70px;
  --btnw: 140px;     /*    ( ) */

  width: min(100%, var(--w));
  margin: var(--mt) auto 0;
  padding: calc(var(--pad) + 6px) calc(var(--pad) + 6px);
  image-rendering: pixelated;
  font-family: 'Minecraftia', monospace;

  /*    +   +   */
  background: #999 url('https://i.ibb.co/rb2TWXL/bgbtn.png') center / cover;
  border: 2px solid #000;
  box-shadow:
    inset -2px -4px #0006,  /* /  */
    inset  2px  2px #FFF7,  /* /  */
    0 8px 24px rgba(0,0,0,.25);
  color: #EEE;
  text-shadow: 2px 2px #000A;
}

/*   (  + ) */
.mc-row {
  position: relative;
  display: flex;
  align-items: center;
  height: var(--h);
  padding: 6px 6px;
  gap: 10px;

  /*      */
  background: #7a7a7a url('https://i.ibb.co/rb2TWXL/bgbtn.png') center / cover;
  border: 2px solid #000;
  box-shadow: inset -2px -4px #0005, inset 2px 2px #FFF5;
}

/*  ( , ) */
.mc-code {
  flex: 1 1 auto;
  min-width: 0;
  padding: 0 10px;
  height: 100%;
  display: flex;
  align-items: center;

  font-size: 14px;
  font-weight: 700;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  user-select: none;
}

/*  (Options  ) */
.mc-btn {
  all: unset;
  flex: 0 0 auto;
  height: calc(var(--h) - 12px);
  min-width: 120px;
  padding: 0 16px;

  display: inline-flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;

  background: #8f8f8f url('https://i.ibb.co/rb2TWXL/bgbtn.png') center / cover;
  color: #EEE;
  border: 2px solid #000;
  box-shadow: inset -2px -4px #0006, inset 2px 2px #FFF7, 0 6px 14px rgba(0,0,0,.25);

  font-family: 'Minecraftia', monospace;
  font-size: 14px;
  line-height: 1;
  text-shadow: 2px 2px #000B;

  transition: filter .15s ease, transform .08s ease, box-shadow .15s ease;
}

.mc-btn:hover {
  filter: brightness(1.08);
  color: #FFFFA0;
  background-color: rgba(100,100,255,.35);
}

.mc-btn:active {
  transform: translateY(1px);
  box-shadow: inset -2px -2px #0006, inset 1px 1px #FFF6, 0 3px 8px rgba(0,0,0,.22);
}

.mc-btn.is-open {
  background: #7d7d7d url('https://i.ibb.co/rb2TWXL/bgbtn.png') center / cover;
}

/*        */
.mc-card.decrypted {
  box-shadow:
    inset -2px -4px rgba(0,0,0,.38),
    inset  2px  2px rgba(255,255,255,.45),
    0 10px 28px rgba(0,200,255,.35);
}

/*  */
@media (max-width: 640px) {
  .mc-card { --h: 52px; --w: calc(100vw - 36px); }
  .mc-btn  { min-width: 96px; font-size: 13px; }
  .mc-code { font-size: 13px; }
}
